import {publicing} from '../api/api.js'
// 
import {wxloginurl} from '../api/request.js'
var {log} = console

class logins{
	// 
	constructor(wxuser) {
	    this.wxuser = wxuser
		log(wxuser)
	}
	
	// 
	async listing(){
		// code
		let userdata = await this.wxlogin()
		log(userdata)
		// 
		let usermen = await this.gologin(userdata)
		return usermen
	}
	
	// code
	wxlogin(){
		let wxuser = this.wxuser
		return new Promise((resolve,reject)=>{
			if(wxuser != undefined){
				wx.login({
					success: (res) => {
						log(res)
						let data = {
							appid:'wxb820c791e554616c',
							secret:'adb3d89546af6e74f2ccb8e16aba51ae',
							nickName:wxuser.nickName,
							avatarUrl:wxuser.avatarUrl,
							code:res.code
						}
						resolve(data)
					},
					fail:(err)=>{
						log(err)
						reject('code')
					}
				})
			}else{
				reject('FAIL')
			}
		})		
	}
	
	// 
	gologin(userdata){
		return new Promise((resolve,reject)=>{
			publicing(wxloginurl,userdata)
			.then((res)=>{
				log(res)
				if(res.data.msg == 'success'){
					// 
					resolve('SUCCESS')
					uni.setStorageSync('usermen',res.data.datas)
				}
			})
			.catch((err)=>{
				log(err)
			})
		})
	}
	
	
	
	// code
	
}

// 
module.exports = logins